.bible-select-container {
  font-family: "Arial", sans-serif;
  margin-top: 10px;
  max-width: 600px;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: #f9f9f9;
}

.title {
  color: #333;
  margin-bottom: 15px;
  font-size: 1em;
  font-weight: bold;
}

.select-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.select-label {
  display: flex;
  flex-direction: column;
  font-size: 0.9em;
  color: #555;
}

.select-box {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 0.9em;
  margin-top: 5px;
}

.select-box:focus {
  border-color: #007bff;
  box-shadow: 0 0 3px #007bff;
  outline: none;
}

.result-container {
  margin-top: 15px;
  padding: 10px;
  border: 1px solid #007bff;
  border-radius: 6px;
  background-color: #eef7ff;
}

.result-text {
  font-size: 1em;
  color: #333;
  font-weight: bold;
  text-align: center;
}

.add-selection-button {
  margin-top: 10px;
  margin-right: 10px;
}
